.img-responsive-heigth {
    display: block;
    max-height: 100%!important;
    width: auto!important;
}

.personaje { width: 100%; height: auto; display: inline-block; background-size: 0%; background-image: url('src/personajes.png');}
.personaje.personaje-F_06 { padding-top: 242.914979757085%; background-position: 61.607992388201716% 100%; background-size: 951.0121457489879% 300%;}

.personaje-w {
    height: 100%; width: auto; padding-left: 14%;
    background-color: turquoise;
    width: 0;
    display: block;
    position: relative;
}
.personaje-w:before{
    content: "";
    display: inline-block;
    background-color: red;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
}

.imagen{
    height: 100%;
    background-color: aquamarine;
}
.imagen:before {
    content: "";
    display: inline-block;
    background-color: red;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
}

svg.personaje {
    height: 100% !important;
    width: auto !important;
    padding-top: 0 !important;
}
